.test-container {
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
}

.answers {
  display: flex;
  flex-direction: column;
  
  align-items: flex-start;
}

.answers button {
  background: transparent;
  text-align: left;
  border-radius: 20px;
  margin: 10px;
  padding: 10px;
  font-size: 20px;
  outline: none;
}

.answers button.active {
  background: rgb(255, 174, 0);
  color: #fff;
}

.popup-result {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 600px;
  background-color: #fff;
  padding: 100px;
  box-shadow: 1px 2px 5px #000;
}

.popup-result li {
  list-style: none;
}

.button_result_bLock {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 600px;
  background-color: #fff;
  padding: 100px;
  box-shadow: 1px 2px 5px #000;
}

.button_result_bLock button {
  font-size: 20px;
  background-color: teal;
  padding: 20px;
  color: #fff;
}
.hidden {
  display: none;
}

.active {
  display: inline-block;
}